import React from 'react';
import classes from './AboutMoreBrand.module.css';

// --DATA--
import image from '../../../assets/images/other/chanel.png';
import SecondaryButton from "../../../ui/components/Buttons/SecondaryButton/SecondaryButton";

const data = {
    image,
    title: '   ',
    text: '                    ',
    links: ['/', '/']
}

const AboutMoreBrand = () => {
    return (
        <div className={classes.wrapper}>
            <div className={classes.content}>
                <div className={classes.inner}>
                    <h3>{data.title}</h3>
                    <p>{data.text}</p>
                </div>
                <img src={data.image} alt='company' />
            </div>
            <div className={classes.bnts}>
                <SecondaryButton></SecondaryButton>
                <SecondaryButton> </SecondaryButton>
            </div>
        </div>
    );
};

export default AboutMoreBrand;